.modal-window .modal-container_content button, .modal-window .modal-container_content p, .modal-window .modal-container_content h1, .container_aside_button, .container_aside_content, .container_aside_title {
  font-family: "Roboto";
  font-style: normal;
}

.modal-window .modal-container_content button, .container_aside_button {
  cursor: pointer;
  background: #E4C1AE;
  border-radius: 32px;
  width: 128px;
  height: 48px;
}

.modal-window .modal-container_content button:hover, .container_aside_button:hover {
  background-color: white;
  color: #E4C1AE;
  box-shadow: 0 0 0 2px #E4C1AE;
  transition: background-color linear 0.2s;
}

.container {
  position: relative;
  display: flex;
  width: 1440px;
  height: auto;
  background: #FFFFFF;
}
.container_image {
  position: absolute;
  width: 505px;
  height: 330px;
  left: 230px;
  top: 96px;
}
.container_aside {
  position: absolute;
  width: 380px;
  height: 250px;
  left: 830px;
  top: 136px;
}
.container_aside_title {
  width: 380px;
  height: 50px;
  left: calc(50% - 190px + 300px);
  top: calc(50% - 25px - 319px);
  color: #121212;
  font-size: 42px;
  font-weight: 700;
  line-height: 120%;
}
.container_aside_content {
  width: 380px;
  height: 96px;
  left: calc(50% - 190px + 300px);
  top: calc(50% - 48px - 222px);
  color: #121212;
  font-size: 16px;
  font-weight: 300;
  line-height: 150%;
}
.container_aside_button {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 16px 40px;
  gap: 10px;
  left: 830px;
  top: 338px;
  border: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #FFFFFF;
}
.modal-window {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transform: scale(1.1);
  transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
}
.modal-window .modal-container {
  position: absolute;
  width: 920px;
  height: 210px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #FFFFFF;
  border-radius: 0.5rem;
  padding: 0 20px;
}
.modal-window .modal-container_content {
  width: 100%;
  height: auto;
}
.modal-window .modal-container_content h1 {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  margin-top: 20px;
}
.modal-window .modal-container_content p {
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
}
.modal-window .modal-container_content .close-button {
  float: right;
  width: 1.5rem;
  text-align: center;
  cursor: pointer;
  color: black;
}
.modal-window .modal-container_content .close-button:hover {
  color: #121212;
  transform: scale(0.8);
  transition: transform ease-in-out 0.2s;
}
.modal-window .modal-container_content button {
  display: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #FFFFFF;
  border: none;
}
.show-modal {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
  transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;
}

.visible {
  display: block !important;
}